.card-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  gap: 3rem;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

.card {
  display: flex;
  flex-direction: column;
  border: 2px solid red;
  width: 25%;
  border-radius: 1rem;
  padding: 1rem;
}
